@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-resumen{

    p{
        margin-bottom: 1rem;
    }

    span{
        color: v.$azul;
        font-weight: v.$bold;
        font-size: 2.4rem;
    }

    h3{
        &:last-of-type{
            margin-top: v.$separacion;
        }
    }
}

.contenedor-servicio{
    padding: 3rem 0;
    border-bottom: 1px solid v.$gris
}