html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #21221c;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 798px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 798px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  position: relative;
  overflow: hidden;
}
@media (min-width: 798px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .imagen img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  object-fit: cover;
}
@media (min-width: 250px) {
  .contenedor-app .imagen img {
    top: -100px;
  }
}
@media (min-width: 375px) {
  .contenedor-app .imagen img {
    top: -200px;
  }
}
@media (min-width: 540px) {
  .contenedor-app .imagen img {
    top: -300px;
  }
}
@media (min-width: 615px) {
  .contenedor-app .imagen img {
    top: -400px;
  }
}
@media (min-width: 798px) {
  .contenedor-app .imagen img {
    top: 0;
    height: 100%;
  }
}
.contenedor-app .app {
  width: 100%;
  margin: 0 auto;
  padding: 3rem 2rem;
}
@media (min-width: 798px) {
  .contenedor-app .app {
    overflow: scroll;
  }
}
.contenedor-app .app::-webkit-scrollbar {
  -webkit-appearance: none;
}
.contenedor-app .app::-webkit-scrollbar:vertical {
  width: 10px;
}
.contenedor-app .app::-webkit-scrollbar-button:increment, .contenedor-app .app .app::-webkit-scrollbar-button {
  display: none;
}
.contenedor-app .app::-webkit-scrollbar:horizontal {
  height: 10px;
}
.contenedor-app .app::-webkit-scrollbar-thumb {
  background-color: #363636;
  border-radius: 20px;
}
.contenedor-app .app::-webkit-scrollbar-track {
  border-radius: 10px;
}

.barra-servicios {
  margin-bottom: 1.25rem;
}
.barra-servicios .boton {
  margin: 1.5rem 0;
}
@media (min-width: 992px) {
  .barra-servicios {
    display: flex;
    justify-content: space-around;
  }
  .barra-servicios .boton {
    padding: 1rem 1.5rem;
    font-size: 1.8rem;
  }
}

.seccion-citas {
  padding: 5rem 0;
  display: none;
}
.seccion-citas .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.barra {
  margin-bottom: 1.5rem;
}
@media (min-width: 798px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra p {
  font-weight: 700;
  font-size: 1.5rem;
}
.barra .boton {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #e6e6e6;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: all;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 5px;
  -webkit-box-shadow: 10px 10px 29px 7px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 29px 7px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 29px 7px rgba(0, 0, 0, 0.75);
  margin: 1.5rem 0;
}
@media (min-width: 798px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: #980000;
  color: #FFFFFF;
  -webkit-box-shadow: 10px 10px 5px 4px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 4px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 4px rgba(0, 0, 0, 0.75);
  cursor: pointer;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 798px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  color: #0da6f3;
  padding: 2rem;
  border-radius: 5px;
  transition: all 0.2s ease-in;
  text-align: center;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
}
.listado-servicios .servicio p {
  color: #21221c;
  margin: 0 0 1.25rem;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.6rem;
  margin: 0;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.4rem;
}
.contenido-resumen h3:last-of-type {
  margin-top: 5rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 1px solid #e1e1e1;
}

.nav-fechas {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 798px) {
  .nav-fechas {
    grid-template-columns: repeat(8, 1fr);
  }
}
.nav-fechas h3 {
  grid-column: 1/3;
  grid-row: 1;
  margin-bottom: 0.5rem;
}
@media (min-width: 798px) {
  .nav-fechas h3 {
    grid-column: 2/8;
  }
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
  border-radius: 2px;
}
.citas li .first-line, .servicios li .first-line {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.citas li:first-child, .servicios li:first-child {
  border-top: none;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #3eb8f5;
  margin: 1rem 0;
}
.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}
.citas li:hover, .servicios li:hover {
  background-color: #34362c;
}
.citas li .total, .servicios li .total {
  text-align: right;
  font-size: 2.4rem;
}

.acciones.acciones-admin {
  margin-top: 1rem;
}
@media (min-width: 798px) {
  .acciones.acciones-admin {
    display: flex;
    justify-content: space-between;
    gap: 1.5rem;
  }
}
.acciones.acciones-admin .boton {
  margin: 1rem 0;
  padding: 1rem 1.5rem;
}

.formulario {
  width: 100%;
  margin-top: 5rem;
}
.formulario.formulario-admin {
  margin-top: 2rem;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input,
.campo select {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled,
.campo select:disabled {
  background-color: #656856;
  color: #FFFFFF;
  cursor: not-allowed;
}

.formulario-admin .campo label {
  flex: 0 0 7rem;
}
.formulario-admin .campo input, .formulario-admin .campo select {
  padding: 0.8333333333rem;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #e6e6e6;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
