  box-shadow: 10px 10px 29px 7px rgba(0, 0, 0, 0.75);
}
@media (min-width: 798px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  color: #FFFFFF;
  -webkit-box-shadow: 10px 10px 5px 4px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 4px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 4px rgba(0, 0, 0, 0.75);
  cursor: pointer;
}
.boton.boton-mostrar, .boton.boton-fecha {
  margin: 1rem 0;
  padding: 1rem 1.5rem;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #e6e6e6;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: all;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 5px;
  -webkit-box-shadow: 10px 10px 29px 7px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 29px 7px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 29px 7px rgba(0, 0, 0, 0.75);
  margin: 1rem 0;
  padding: 1rem 1.5rem;
}
@media (min-width: 798px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #980000;
  color: #FFFFFF;
  -webkit-box-shadow: 10px 10px 5px 4px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 4px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 4px rgba(0, 0, 0, 0.75);
  cursor: pointer;
}

.animatedButton {
  overflow: hidden;
  padding: 1.5rem 4rem;
  border: none;
  border-radius: 5px;
  background-color: #0da6f3;
  color: #FFFFFF;
  font-weight: 700;
  width: 100%;
  text-align: center;
  position: relative;
  display: inline-block;
  cursor: pointer;
  margin-bottom: 1rem;
}
@media (min-width: 798px) {
  .animatedButton {
    width: auto;
  }
}
.animatedButton span {
  position: relative;
  transition: color 0.6s cubic-bezier(0.53, 0.21, 0, 1);
}
.animatedButton:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  transform: scaleX(0);
  transform-origin: 100% 100%;
  transition: transform 0.6s cubic-bezier(0.53, 0.21, 0, 1);
}
.animatedButton#anterior:hover:before {
  transform-origin: 1 0;
  transform: scaleX(1);
}
.animatedButton#anterior:hover span {
  color: #0da6f3;
}
.animatedButton#siguiente:hover:before, .animatedButton.reservacita:hover:before {
  transform-origin: 0 0;
  transform: scaleX(1);
}
.animatedButton#siguiente:hover span, .animatedButton.reservacita:hover span {
  color: #0da6f3;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 798px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta,
.alertaResumen {
  padding: 1.25rem;
  color: #FFFFFF;
  border-radius: 5px;
  width: 100%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
}
.alerta.error,
.alertaResumen.error {
  background-color: #cb0000;
}
.alerta.exito,
.alertaResumen.exito {
  background-color: #329f00;
}

@media (min-width: 798px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #0da6f3;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
